@import url('https://fonts.googleapis.com/css?family=Oswald');
body {
	background-color: #261D2B;
	text-align: center;
}
.DiveInButton {
	-webkit-transition-timing-function: ease-in;
	     -o-transition-timing-function: ease-in;
	        transition-timing-function: ease-in;
	position: absolute;
	top:45%;
	left:40%;
	right:40%;
	bottom:45%;
	font-family: 'Oswald', sans-serif;
	font-size: 30px;
	border:3px solid #61A8D6;
	color:#61A8D6;
	background-color: #261D2B;
	width: 20%;
	height: 10%;
	-webkit-transition: 1s all;
	-o-transition: 1s all;
	transition: 1s all;
}
.DiveInButton:hover {
	border:3px solid #F8F27D;
	background-color: #F8F27D;
	color:#261D2B;
}
.DiveInButton div{
	transform: scale(1);
	transition: 1s transform;
}
.DiveInButton:hover div{
	transform: scale(1.5);
	transition: 1s transform;
}